import React from 'react';
import '../../../src/components/intro/intro.css';
import bg from '../../../src/assets/image.png';
import bgSmall from '../../../src/assets/image-small.png';
import btnImg from '../../../src/assets/hireme.png';
import { Link } from 'react-scroll';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';
import { themeContext } from '../../context'
import { useContext ,useState} from 'react';

const Intro = () => {
  useEffect(() => {
    AOS.init({
      duration: 1000,
      delay: 100,
    });
  }, []);
  const theme = useContext(themeContext);
  const lightmode = theme.state.lightmode;
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);
  useEffect(() => {
    const handleResize = () => {
      setWindowWidth(window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (

    <section id="intro">
      <picture className='image-container'>
        <source media="(max-width: 830px)" srcSet={bgSmall} />
        <img src={bg} alt='Profile' className='bg' style={{border: (windowWidth<830 && lightmode)? '0.07rem solid black':''}} />
      </picture>
      <div className='introContent'>
        <span style={{ fontWeight: lightmode ? '400' : '' }} className='hello'>Hello, </span>
        <span className='introText'>I'm <span className='introName'>Tensaey</span> <br />a Website Developer</span>
        <p style={{ fontWeight: lightmode ? '400' : '' }} className='introPara'>I am a skilled web developer with experience <br />in creating visually appealing and user friendly websites.</p>
        <Link activeClass='active' to='contact' smooth={true} offset={-100} duration={500}><button className='btn animate__animated animate__infinite animate__pulse'><img src={btnImg} alt='Hire Me' className='btnImg' /> Hire Me</button></Link>
      </div>

    </section>
  )
}

export default Intro;