import React from 'react';
import '../../../src/components/clients/client.css'
import Walmart from '../../../src/assets/walmart.png';
import Microsoft from '../../../src/assets/microsoft.png';
import Adobe from '../../../src/assets/adobe.png';
import Facebook from '../../../src/assets/facebook.png';
import { themeContext } from '../../context'
import { useContext } from 'react';

const Clients = () => {
    const theme = useContext(themeContext);
    const lightmode = theme.state.lightmode;
    return (
        <section id='clients'>
            <span data-aos="zoom-in-up"><h1 className='clientPageTitle'>My Clients</h1></span>
            <span data-aos="fade-left">
                <p className='clientsDesc'>
                    I have had the opportunity to work with a diverse group of companies.
                    Some of the notable companies I have worked with includes
                </p>
            </span>
            <div className='clientImgs'>
                <span data-aos="fade-down"><img src={Walmart} alt='Client' className='clientImg' /> </span>
                <span data-aos="fade-right"><img src={Adobe} alt='Client' className='clientImg' /></span>
                <span data-aos="fade-left"><img src={Microsoft} alt='Client' className='clientImg' /></span>
                <span data-aos="fade-up"><img src={Facebook} alt='Client' className='clientImg' /></span>
            </div>
        </section>
    )
}

export default Clients;