import React from 'react';
import '../../../src/components/works/works.css';
import Portfolio1 from '../../../src/assets/portfolio-1.png';
import Portfolio2 from '../../../src/assets/portfolio-2.png';
import Portfolio3 from '../../../src/assets/portfolio-3.png';
import Portfolio4 from '../../../src/assets/portfolio-4.png';
import Portfolio5 from '../../../src/assets/portfolio-5.png';
import Portfolio6 from '../../../src/assets/portfolio-6.png';
const Works = () => {
  return (
    <section id='works'>
        <span data-aos="zoom-in-up"><h2 className='worksTitle'>My Portfolio</h2></span>
        <span data-aos="fade-left" className='worksDesc'>Explore my portfolio of websites, where design meets functionality to create unforgettable online experiences. From elegant user interfaces to responsive layouts, each project showcases a commitment to innovation and user-centric design. Join me on this digital journey as we transform ideas into visually captivating and technically flawless web solutions.</span>
        <div className='worksImgs'>
            <span data-aos="fade-down"><img src={Portfolio1} alt='Portfolio Image' className='worksImg'/></span> 
            <span data-aos="fade-right"><img src={Portfolio2} alt='Portfolio Image' className='worksImg'/></span> 
            <span data-aos="fade-up"><img src={Portfolio3} alt='Portfolio Image' className='worksImg'/></span> 
            <span data-aos="fade-up"><img src={Portfolio4} alt='Portfolio Image' className='worksImg'/></span> 
            <span data-aos="fade-left"><img src={Portfolio5} alt='Portfolio Image' className='worksImg'/></span> 
            <span data-aos="fade-down"><img src={Portfolio6} alt='Portfolio Image' className='worksImg'/></span> 
        </div>
    </section>
  )
}

export default Works;