import React from 'react';
import '../../../src/components/skills/skills.css';
import UIDesign from '../../../src/assets/ui-design.png';
import WebDesign from '../../../src/assets/website-design.png';
import { themeContext } from '../../context'
import { useContext } from 'react';

const Skills = () => {
    const theme = useContext(themeContext);
    const lightmode = theme.state.lightmode;
    return (
        <section id='skills'>
            <span className='skillTitle' data-aos="zoom-in-up">What I do</span>
            <span className='skillDesc' data-aos="fade-right">I am a skilled and passionte web developer with experience in creating visually appealing and user-friendly websites. I have a strong understanding of design and a keen eye for detail. I am proficient in HTML, CSS, and JavaScript, as well as design software such as Adobe Photoshop and Illustrator.</span>
            <div className='skillBars'>
                <div className='skillBar' data-aos="fade-left" style={{background: lightmode? 'rgb(150, 150, 150)' : '', color : lightmode? 'black':''}}>
                    <img src={UIDesign} alt='UIDesing' className='skillBarImg' />
                    <div className='skillBarText'>
                        <h2>UI/UX Design</h2>
                        <p style={{ fontWeight: lightmode ? '300' : '' }}>I craft engaging and intuitive user experiences through thoughtful UI/UX design.</p>
                    </div>
                </div>
                <div className='skillBar' data-aos="fade-right" style={{background: lightmode? 'rgb(150, 150, 150)' : '', color : lightmode? 'black':''}}>
                    <img src={WebDesign} alt='WebDesign' className='skillBarImg' />
                    <div className='skillBarText'>
                        <h2>Website Developing</h2>
                        <p style={{ fontWeight: lightmode ? '300' : '' }}>I bring web ideas to life through expert coding and development.</p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skills