import React, { useState, useEffect, useRef } from 'react';
import Toggle from '../../../src/components/toggle/toggle';
import '../../../src/components/navbar/navbar.css';
import logo from '../../../src/assets/logo.png';
import logoB from '../../../src/assets/logo-black.png';
import { Link } from 'react-scroll';
import { themeContext } from '../../context';
import { useContext } from 'react';
import 'animate.css';

const Navbar = () => {
  const [showMenu, setShowMenu] = useState(false);
  const theme = useContext(themeContext);
  const lightmode = theme.state.lightmode;
  const logoSrc = lightmode ? logoB : logo;
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const menuButtonRef = useRef(null);
  const mobMenuRef = useRef(null);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
    setShowMenu(!showMenu);
  };

  useEffect(() => {
    const handleClickOutsideMenu = (event) => {
      if (
        mobMenuRef.current &&
        !mobMenuRef.current.contains(event.target) &&
        !menuButtonRef.current.contains(event.target)
      ) {
        setIsMenuOpen(false);
        setShowMenu(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutsideMenu);

    return () => {
      document.removeEventListener('mousedown', handleClickOutsideMenu);
    };
  }, []);

  return (
    <nav
      className="navbar"
      style={{ background: lightmode ? '#cac6c6' : '', color: lightmode ? 'black' : '' }}
    >
      <img
        src={logoSrc}
        alt="Logo"
        className="logo"
        onClick={() => {
          window.scrollTo({ top: 0, behavior: 'smooth' });
        }}
      />
      <div className="toggleContainer">
        <Toggle />
        <div className="desktopMenu">
          <Link activeClass='active' to='intro' spy={true} smooth={true} offset={-100} duration={500} className='desktopMenuListItem'>Home</Link>
          <Link activeClass='active' to='skills' spy={true} smooth={true} offset={-110} duration={500} className='desktopMenuListItem'>About</Link>
          <Link activeClass='active' to='works' spy={true} smooth={true} offset={-100} duration={500} className='desktopMenuListItem'>Portfolio</Link>
          <Link activeClass='active' to='clients' spy={true} smooth={true} offset={-100} duration={500} className='desktopMenuListItem'>Clients</Link>
          <Link activeClass='active' to='contact' spy={true} smooth={true} offset={-100} duration={500} className='desktopMenuListItem'>Contact</Link>
        </div>
        <div className={`mobMenu ${isMenuOpen ? 'clicked' : ''}`} ref={mobMenuRef}>
          <button
            ref={menuButtonRef}
            className={`mobMenu ${isMenuOpen ? 'opened' : ''}`}
            onClick={toggleMenu}
            aria-expanded={!isMenuOpen}
            aria-label="Main Menu"
          >
            <svg className='ham' width="50" height="40" viewBox="0 0 100 100">
              <path
                className="line line1"
                d="M 20,29.000046 H 80.000231 C 80.000231,29.000046 94.498839,28.817352 94.532987,66.711331 94.543142,77.980673 90.966081,81.670246 85.259173,81.668997 79.552261,81.667751 75.000211,74.999942 75.000211,74.999942 L 25.000021,25.000058"
              />
              <path className="line line2" d="M 20,50 H 80" />
              <path
                className="line line3"
                d="M 20,70.999954 H 80.000231 C 80.000231,70.999954 94.498839,71.182648 94.532987,33.288669 94.543142,22.019327 90.966081,18.329754 85.259173,18.331003 79.552261,18.332249 75.000211,25.000058 75.000211,25.000058 L 25.000021,74.999942"
              />
            </svg>
          </button>

          <div
            className="navMenu animate__animated animate__backInDown"
            style={{ display: showMenu ? 'flex' : 'none', background: lightmode ? 'rgb(150,150,150)' : '' }}
          >
            <Link style={{ background: lightmode ? '#cac6c6' : '' }} activeClass='active' to='intro' spy={true} smooth={true} offset={-100} duration={500} className='listItem animate__animated animate__backInRight animate__delay-1s'>Home</Link>
            <Link style={{ background: lightmode ? '#cac6c6' : '' }} activeClass='active' to='skills' spy={true} smooth={true} offset={-110} duration={500} className='listItem animate__animated animate__backInRight animate__delay-1s'>About</Link>
            <Link style={{ background: lightmode ? '#cac6c6' : '' }} activeClass='active' to='works' spy={true} smooth={true} offset={-100} duration={500} className='listItem animate__animated animate__backInRight animate__delay-1s'>Portfolio</Link>
            <Link style={{ background: lightmode ? '#cac6c6' : '' }} activeClass='active' to='clients' spy={true} smooth={true} offset={-100} duration={500} className='listItem animate__animated animate__backInRight animate__delay-1s'>Clients</Link>
            <Link style={{ background: lightmode ? '#cac6c6' : '' }} activeClass='active' to='contact' spy={true} smooth={true} offset={-100} duration={500} className='listItem animate__animated animate__backInRight animate__delay-1s'>Contact</Link>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
